
    .tued-simplemap {
        position: relative;
        z-index: 1;

        .tued-simplemap-overlay {
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(0,0,0,.2);
            transition: .12s all;
            z-index: 999999;
            opacity: 0;

            button {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }

        .tued-simplemap-editable-overlay {
            position: absolute;
            z-index: 999999;
            top: 0;
            right: 0;
            padding: 5px;
            display: none;
        }

        &:hover .tued-simplemap-overlay {
            opacity: 1;
        }

        &.tued-simplemap-editable {
            .tued-simplemap-overlay {
                display: none;
            }
            .tued-simplemap-editable-overlay {
                display: block;
            }
        }
    }
